import { Component, OnInit, ViewChild } from '@angular/core';
import { Producto } from 'src/app/model/producto';
import { MatPaginator, MatSort, MatTableDataSource, MatDialog } from '@angular/material';
import { Router } from '@angular/router';
import { ProductoService } from 'src/app/service/producto.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-list-producto',
  templateUrl: './list-producto.component.html',
  styleUrls: ['./list-producto.component.css']
})
export class ListProductoComponent implements OnInit {

  productos:Producto[];
  searchProducto:string;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort:MatSort;

  dataSource:MatTableDataSource<Producto>;
  displayedColumns = ['codigo','descripcion','existencia','familia','marca','compra','venta','accion'];

  applyFilter(filterValue:string){
    this.dataSource.filter=filterValue.trim().toUpperCase();
  }
  constructor(
  public dialog: MatDialog,
  private router: Router,
  private productoService: ProductoService,
  private flashMensaje:FlashMessagesService){
    this.dataSource=new MatTableDataSource;
   }

  ngAfterViewInit(){
    this.dataSource.paginator=this.paginator;
    this.dataSource.sort=this.sort;
  }

  ngOnInit() {
     this.list();
  }


  list(){

    this.productoService.getProducto().subscribe( productos => {
      this.productos=productos 
      this.dataSource.data =this.productos;
    },error => {
      this.flashMensaje.show('Error al conectar con el servidor...',{cssClass:'alert-danger'})
      }
      );

  }

  search(searchProducto){
    this.productoService.getBuscar(this.searchProducto).subscribe( data=>{
      this.productos=data;
      this.dataSource.data=this.productos;
    })
  }
  
  

  
  addProducto(): void {
    this.router.navigate(['add-producto']);
  }

  remove(producto: Producto){
    if(confirm("Seguro que quieres eliminar: "+producto.descripcion)){
      this.productoService.delete(producto).subscribe(() =>{
        this.list();
      }, error =>{
        this.flashMensaje.show("No se puede eliminar producto.",{cssClass:'alert-danger'})
      }) 
   }  
    }

}
