import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { User } from '../model/user';
import { GLOBAL } from './global';
import { Router } from '@angular/router';

const httpOptions = { headers:new HttpHeaders({
  'Content-Type':'application/json',
  'Authorization':localStorage.getItem("Authorization")
})}


@Injectable()
export class UserService {
      url:string;
  constructor(private http: HttpClient, private _router: Router) {
    this.url=GLOBAL.url;
   }

  listUser(){
    return this.http.get<User[]>(this.url+'/usuario', httpOptions);
  }
   

  getUserLogin(user){
    return this.http.post<any>(this.url+'/auth', user);
  }

  getUserDetails(){
    return this.http.get<any>(this.url+'/user',httpOptions);
  }

  logoutUser(){
    if(confirm("Seguro que quieres cerrar la sesion ")){
      localStorage.removeItem("Authorization");
      localStorage.removeItem("username");
      localStorage.removeItem("id");
      this._router.navigate(['/login']);
      
      }
   } 

  loggedIn(){
    return !!localStorage.getItem("Authorization");
  }

  getToken(){
    return localStorage.getItem('Authorization');
  }

  registerUser(user:User){
    console.log(JSON.stringify(user))
      return this.http.post<User>(this.url+'/usuario', user, httpOptions);
  }

  removerUser(user){
    return this.http.post<User>(this.url+'/usuario/eliminar',user, httpOptions);
  }

}
