import { Component, OnInit, ViewChild } from '@angular/core';
import { Proveedor } from '../../../model/proveedor';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { ActivatedRoute, Router } from '@angular/router';
import { ProveedorService } from '../../../service/proveedor.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-list-proveedor',
  templateUrl: './list-proveedor.component.html',
  styleUrls: ['./list-proveedor.component.css']
})
export class ListProveedorComponent implements OnInit {
 
  proveedors:Proveedor[];
  searchProveedor:string;

  dataSource:MatTableDataSource<Proveedor>;
  displayedColumns = ['id','nombre','ruc','alta','accion'];
  
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort:MatSort;

constructor(
      private route: ActivatedRoute,
      private proveedorService: ProveedorService,
      private router: Router,
      public flashMessaje:FlashMessagesService
      ) { 
        this.dataSource=new MatTableDataSource;
      }
      ngAfterViewInit(){
        this.dataSource.paginator=this.paginator;
        this.dataSource.sort=this.sort;
      }

ngOnInit() {

 this.list();

     
}

list(){
  this.proveedorService.getProveedors().subscribe( proveedors => {
    this.proveedors=proveedors 
    this.dataSource.data =this.proveedors;
  },error => {
    this.flashMessaje.show(error.error.message,{cssClass:'alert-danger', timeout:4000})
    }
    );
}

 addProveedor(): void {
  this.router.navigate(['add-proveedor']);
};

remove(proveedor:Proveedor){
  if(confirm("Seguro que quieres eliminar: "+proveedor.nombre)){
    this.proveedorService.delete(proveedor).subscribe(() =>{
      this.list();
    }, error =>{
     window.alert("No se puede eliminar proveedor.")
    }) 
 }

 
  }

  search(searchaProveedor){
    this.proveedorService.getBuscar(this.searchProveedor).subscribe( proveedors=>{
      this.proveedors=proveedors;
      this.dataSource.data=this.proveedors;
    },error => {
      console.log(error);
    })
  }

  applyFilter(){
    if(this.searchProveedor !=""){
      this.dataSource.data=this.dataSource.data.filter(res=>{
        return res.nombre.toLocaleLowerCase().match(this.searchProveedor.toLocaleLowerCase());
      });
    } else if(this.searchProveedor=="") {
      this.ngOnInit();
    }
}

}

