import { Component, OnInit, ViewChild, Inject, Output, EventEmitter } from '@angular/core';
import { FamiliaService } from '../../../service/familia.service';
import { Familia } from '../../../model/familia';
import { Router } from '@angular/router';
import { MatTableDataSource, MatPaginator, MatSort, MAT_DIALOG_DATA } from '@angular/material';

@Component({
  selector: 'app-list-familia',
  templateUrl: './list-familia.component.html',
  styleUrls: ['./list-familia.component.css']
})
export class ListFamiliaComponent implements OnInit {

  @Output() familiaE=new EventEmitter();
  

  errorMessage:string
  familias:Familia[];
  familia:Familia;
  dataSource;
  displayedColumns = ['id','descripcion','estado','editar','eliminar'];
  

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort:MatSort;

  applyFilter(filterValue:string){
  this.dataSource.filter=filterValue.trim().toLowerCase();

  }
  constructor(
    private familiaService:FamiliaService,
    private router:Router,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit() {
    this.list();
  }

  list(){
    this.familiaService.getFamilia().subscribe( familias => {
      this.familias=familias 
      this.dataSource =new MatTableDataSource(this.familias);
    },error => {
        this.errorMessage=<any>error;
         console.log(this.errorMessage);
      }
      );
  }

editar(familia){
    this.familiaE.emit({fa:familia});
  }

  remove(familia:Familia){
    if(confirm("Seguro que quieres eliminar: "+familia.descripcion)){
      this.familiaService.delete(familia).subscribe(() =>{
        this.list();
      }, error =>{
       window.alert("Error no se puede eliminar")
      }) 
   }  
    }

}


