import { Component, OnInit } from '@angular/core';
import { Proveedor } from '../../../model/proveedor';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ProveedorService } from '../../../service/proveedor.service';

@Component({
  selector: 'app-edit-proveedor',
  templateUrl: './edit-proveedor.component.html',
  styleUrls: ['./edit-proveedor.component.css']
})
export class EditProveedorComponent implements OnInit {

  proveedor:Proveedor;
  constructor(
  private router: Router,
  private proveedorService:ProveedorService,
  private route:ActivatedRoute) { }

  ngOnInit() {
     this.route.params.forEach((params: Params) =>{
      let id = params['id'];
      this.proveedorService.getProveedorId(id).subscribe(data => {
        this.proveedor=data;
      });
    });
    
  }
  
    save(proveedor: Proveedor){
    this.proveedorService.save(proveedor).subscribe(proveedor =>{
      this.router.navigate(['list-proveedor']);
    });
  }
  

}
