import { Producto } from '../model/producto';
import { HttpHeaders } from '@angular/common/http';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { GLOBAL } from './global';

const httpOptions = { headers:new HttpHeaders({
  'Content-Type':'application/json',
  'Authorization':localStorage.getItem("Authorization")
})}
@Injectable({
  providedIn: 'root'
})
export class ProductoService {
  public url:string;
  constructor(public http:HttpClient) {
    this.url=GLOBAL.url;
  }

   getProducto (){
    return this.http.get<Producto[]>(this.url+'/producto',httpOptions);
  }
  getBuscar(descripcion:string){
    return this.http.get<Producto[]>(this.url+'/producto/buscar/'+descripcion, httpOptions);
  }

  getProductoId(id){
    return this.http.get<Producto>(this.url+'/producto/'+id,httpOptions);
  }

  getProductoConsultaId(id){
    return this.http.get<Producto>(this.url+'/producto/id/'+id,httpOptions);
  }
  
  save(producto: Producto){
  	return this.http.post<Producto>(this.url+'/producto' , producto,httpOptions);
    }

    actualizar(producto: Producto){
      return this.http.post<Producto>(this.url+'/producto' , producto,httpOptions);
      }


    update(producto: Producto){
      return this.http.put<Producto>(this.url+'/producto' , producto,httpOptions);
      }
  
    delete(producto: Producto){
      return this.http.post<Producto>(this.url+'/producto/eliminar',producto,httpOptions);
      }
    
  
  
}
