import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { Route, Router } from '@angular/router';
import { FamiliaService } from '../../../service/familia.service';
import { Familia } from '../../../model/familia';
import { ListFamiliaComponent } from '../list-familia/list-familia.component';
import { MAT_DIALOG_DATA } from '@angular/material';

@Component({
  selector: 'app-add-familia',
  templateUrl: './add-familia.component.html',
  styleUrls: ['./add-familia.component.css']
})
export class AddFamiliaComponent implements OnInit {

  @ViewChild(ListFamiliaComponent) hijo: ListFamiliaComponent;

  familia:Familia;
  constructor(
    public familiaService:FamiliaService,
    @Inject(MAT_DIALOG_DATA) public data: any
  ){
    this.familia={id:null,descripcion:"", estado:true};
    
  }

  ngOnInit(){

  }

  recibirDato(familia){
      this.familia={id:familia.fa.id,descripcion:familia.fa.descripcion,estado:familia.fa.estado};
}
  
  submit(){
    if(this.familia.id==null){
      this.familiaService.save(this.familia).subscribe(result => {
        this.familia={id:null,descripcion:"",estado:true}
        this.hijo.list();
      })

    } else {
      this.familiaService.save(this.familia).subscribe(result =>{
        this.familia={id:null,descripcion:"",estado:true}
        this.hijo.list();
      })
    }
  }

}
