import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { Proveedor } from '../../../model/proveedor';
import { MatPaginator, MatSort, MatTableDataSource, MAT_DIALOG_DATA } from '@angular/material';
import { ActivatedRoute, Router } from '@angular/router';
import { ProveedorService } from '../../../service/proveedor.service';

@Component({
  selector: 'app-prov-dialog',
  templateUrl: './prov-dialog.component.html',
  styleUrls: ['./prov-dialog.component.css']
})
export class ProvDialogComponent implements OnInit {


  titulo='Lista Proveedor';
  proveedors:Proveedor[];
  errorMessage:string;

  dataSource;
  displayedColumns = ['nombre','ruc','telefono','pasar'];
  
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort:MatSort;

applyFilter(filterValue:string){
this.dataSource.filter=filterValue.trim().toLowerCase();
}
constructor(
      private route: ActivatedRoute,
      private proveedorService: ProveedorService,
      private router: Router,
      @Inject(MAT_DIALOG_DATA) public data: any,
      ) { }

ngOnInit() {

  this.proveedorService.getProveedors().subscribe( proveedors => {
      this.proveedors=proveedors 
      this.dataSource =new MatTableDataSource(this.proveedors);
    },error => {
        this.errorMessage=<any>error;
         console.log(this.errorMessage);
      }
      );

     
}


  }
