import { Injectable } from '@angular/core';
import { Familia } from '../model/familia';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { GLOBAL } from './global';
const httpOptions = { headers:new HttpHeaders({
  'Content-Type':'application/json',
  'Authorization':localStorage.getItem("Authorization")
})}
@Injectable({
  providedIn: 'root'
})
export class FamiliaService {

  public url:string;
  constructor(public http:HttpClient) {
    this.url=GLOBAL.url;
  }

getFamilias(){

  return this.http.get<Familia>(this.url+'/familia',httpOptions)
}
getFamilia(){

  return this.http.get<Familia[]>(this.url+'/familia',httpOptions)
}

getFamiliaId(id){
  return this.http.get<Familia>(this.url+'/familia/'+id,httpOptions);
}

save(familia:Familia){
  return this.http.post<Familia>(this.url+'/familia',familia,httpOptions)
}

update(familia:Familia){
  return this.http.put<Familia>(this.url+'/familia',familia,httpOptions)
}

delete(familia: Familia){
  return this.http.post<Familia>(this.url+'/familia/eliminar',familia, httpOptions);
}


}
