import { Component, OnInit,Output,EventEmitter} from '@angular/core';
import { MatDialog, MatTableDataSource } from '@angular/material';
import { Cliente } from '../../../model/cliente';
import { CreditoService } from '../../../service/credito.service';
import { Credito } from '../../../model/credito';
import { ClienteDialogComponent } from '../../venta/cliente-dialog/cliente-dialog.component';

@Component({
  selector: 'app-add-credito',
  templateUrl: './add-credito.component.html',
  styleUrls: ['./add-credito.component.css']
})
export class AddCreditoComponent implements OnInit {

  @Output() creditoE=new EventEmitter();


  cliente:Cliente;
  credito:Credito[];
  id:number;
  nombre:string;
  estado:string;
  estadoTodo:string;
  estadoPago:string;
  dataSource;
  displayedColumns = ['usuario','fecha','plazo','estado','importe','pagado','restante','seleccionar'];

constructor(public dialog: MatDialog,
            private creditoService:CreditoService) {
              this.id=0;
             }

ngOnInit() {

}

openDialog() {
  let open=this.dialog.open(ClienteDialogComponent, {
    height: '100%',
    width: '100%',
    panelClass:['colorDialog']
  });

  open.afterClosed().subscribe(result => {
    this.cliente = result;
    this.id=this.cliente.id;
    this.nombre=this.cliente.nombre+" , "+this.cliente.apellido;
  });
}

filtrar(){
    if(this.estadoPago=="estadoTodo"){
    let idf=this.id;
    console.log(idf);
    this.creditoService.getCreditoTodo(idf).subscribe( data =>{
      this.credito=data;
      this.dataSource =new MatTableDataSource(this.credito);
  },error =>{
    window.alert("Registro buscado no existe..")
  })
  }else if(this.estadoPago=="NO"){
  let idc=this.id;
  let estadoe="NO";
  this.creditoService.getCreditoPago(idc,estadoe).subscribe( data =>{
    this.credito=data;
    this.dataSource =new MatTableDataSource(this.credito);
  },error =>{
    window.alert("Registro buscado no existe..")
  })
}else if(this.estadoPago=="SI"){
  let id=this.id;
  let estado="SI";
  this.creditoService.getCreditoPago(id,estado).subscribe( data =>{
    this.credito=data;
    this.dataSource =new MatTableDataSource(this.credito);
  },error =>{
    window.alert("Registro buscado no existe..")
  })
}
}


enviar(credito:Credito){
  let c=credito.id;
  let i=credito.importe;
  let p=credito.pagado;
  let total=i-p;
  this.creditoE.emit({saldo:total,c:c});
}
}
