import { Component, OnInit } from '@angular/core';
import { Producto } from 'src/app/model/producto';
import { Familia } from 'src/app/model/familia';
import { ProductoService } from 'src/app/service/producto.service';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-add-producto',
  templateUrl: './add-producto.component.html',
  styleUrls: ['./add-producto.component.css']
})
export class AddProductoComponent implements OnInit {

  public producto: Producto = new Producto;
  public familia:Familia[];
  success:string;
  errorMessage:string;
  familiaId:number;
  marcaId:number;
  unidadMedidaId:number;

  public ultimoI:Array<any>=[];
  ultimoCodigo:number;
  constructor(
  private productoService: ProductoService,
  private router: Router,
  public flashMensaje:FlashMessagesService
  ) { }


  ngOnInit(){
  
  }



  
  playAudio(){
    let audio=new Audio();
    audio.src=('../../../assets/sonido/Error.wav');
    audio.load();
    audio.play();
  }

  guar(event){
    
    this.familiaId=event.fa;
    this.marcaId=event.ma;
    this.unidadMedidaId=event.uni;

    let id=this.producto.id;
    let cod=this.producto.codigoProducto;
    let d=this.producto.descripcion;
    let pc=this.producto.precioCosto;
    let pv=this.producto.precioVenta;


    this.producto={id:null,codigoProducto:cod,descripcion:d,existencia:null, precioCosto:pc, precioVenta:pv,unidadMedida:{id:this.unidadMedidaId,descripcion:null},familia:{id:this.familiaId,descripcion:null},marca:{id:this.marcaId,descripcion:null}};
    this.productoService.save(this.producto).subscribe(() =>{
       // this.flashMensaje.show('Producto ha sido guardado..',{cssClass:'alert-success', timeout:4000})

    },error => {
      console.log(error)
      //this.flashMensaje.show(error.error.message,{cssClass:'alert-danger', timeout:4000})
    });
  }
  

}
