import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Marca } from '../model/marca';
import { GLOBAL } from './global';

const httpOptions = { headers:new HttpHeaders({
  'Content-Type':'application/json',
  'Authorization':localStorage.getItem("Authorization")
})}

@Injectable({
  providedIn: 'root'
})
export class MarcaService {;

  public url:string;
  constructor(public http:HttpClient) {
    this.url=GLOBAL.url;
  }

   getMarcas(){
    return this.http.get<Marca[]>(this.url+'/marca', httpOptions);
   }
   getMarca(){
    return this.http.get<Marca>(this.url+'/marca', httpOptions);
   }

   save(marca:Marca){
     return this.http.post<Marca>(this.url+'/marca',marca, httpOptions);
   }
   update(marca:Marca){
    return this.http.put<Marca>(this.url+'/marca',marca, httpOptions);
  }

   delete(marca: Marca){
    return this.http.post<Marca>(this.url+'/marca/eliminar',marca, httpOptions);
  }
  
}
