import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { Marca } from '../../../model/marca';
import { MarcaService } from '../../../service/marca.service';
import { ListMarcaComponent } from '../list-marca/list-marca.component';
import { MAT_DIALOG_DATA } from '@angular/material';

@Component({
  selector: 'app-add-marca',
  templateUrl: './add-marca.component.html',
  styleUrls: ['./add-marca.component.css']
})
export class AddMarcaComponent implements OnInit {

  @ViewChild(ListMarcaComponent) hijo: ListMarcaComponent;

  marca:Marca;
  constructor(
    public marcaService:MarcaService,
    @Inject(MAT_DIALOG_DATA) public data: any
  ){
    this.marca={id:null,descripcion:"", estado:true};
    
  }

  ngOnInit(){

  }

  recibirDato(marca){
      this.marca={id:marca.fa.id,descripcion:marca.fa.descripcion,estado:marca.fa.estado};
}
  
  submit(){
    if(this.marca.id==null){
      this.marcaService.save(this.marca).subscribe(result => {
        this.marca={id:null,descripcion:"",estado:true}
        this.hijo.list();
      })

    } else {
      this.marcaService.save(this.marca).subscribe(result =>{
        this.marca={id:null,descripcion:"",estado:true}
        this.hijo.list();
      })
    }
  }

}
