import { Component, OnInit, ViewChild, AfterViewInit} from '@angular/core';
import { FacturaService } from '../../../service/factura.service';
import { Router } from '@angular/router';
import { Factura } from '../../../model/factura';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import {  DatePipe } from '@angular/common';
import { DetalleFactura } from 'src/app/model/detalle-factura';

@Component({
  selector: 'app-list-venta',
  templateUrl: './list-venta.component.html',
  styleUrls: ['./list-venta.component.css']
})
export class ListVentaComponent implements OnInit {

  factura:Factura[];
  facturaArray:Factura[];
  facturaA:Factura;
  detalle:DetalleFactura[];
  bfecha:Date;
  fec=new Date;
  dateNow:Date=new Date();
  dateNowISO=this.dateNow.toISOString();
  searchaVenta:string;


  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort:MatSort;

  dataSource:MatTableDataSource<Factura>;
  displayedColumns=['codigo','nombre','subtotal','fecha','anular'];


  constructor(
    private facturaService: FacturaService,
    private router: Router
  ) { 
    this.dataSource=new MatTableDataSource;
  }

  ngAfterViewInit(){
    this.dataSource.paginator=this.paginator;
    this.dataSource.sort=this.sort;
  }
  ngOnInit() {
    this.list();
  }

  bfechaC(){
    if(this.bfecha==null){
      this.list();
    } else {
    this.facturaService.getPorFecha(this.bfecha).subscribe(result=>{
      this.factura=result;
      this.dataSource.data=this.factura;
    })
  }
  }

  list(){

    this.facturaService.getFactura().subscribe( data => {
      this.factura=data
      this.dataSource.data =this.factura;
    },error => {
        
      }
      );

  }

 
  listaPorFechaHoy(){
  var datePipe=new DatePipe('en-US');
  this.facturaService.getPorFecha(datePipe.transform(new Date(),'yyyy-MM-dd')).subscribe(result=>{
    this.factura=result;
    this.dataSource.data=this.factura;
  })
  }

 
  addVenta(){
    this.router.navigate(['add-venta']);
  }

  applyFilter(){
    if(this.searchaVenta !=""){
      this.dataSource.data=this.dataSource.data.filter(res=>{
        return res.cliente.nombre.toLocaleLowerCase().match(this.searchaVenta.toLocaleLowerCase());
      });
    } else if(this.searchaVenta=="") {
      this.ngOnInit();
    }
}


  anular(factura:Factura){
    let idDetalle=factura.id;
    this.facturaService.getDetalleId(idDetalle).subscribe( data=>{
        this.detalle=data;
    })

    if(factura.anulado=="SI"){
      window.alert("La factura ya ha sido anulada")
    } else {
    if(confirm("Seguro que quieres anular")){
      let id=factura.id;
      this.facturaA={id:id,subTotal:null,anulado:"SI",estado:null,user:{id:null},cliente:{id:null,nombre:null}};
      this.facturaService.anular(this.facturaA).subscribe(() => {
                    this.list();
                           window.alert("La compra ha sido cancelada..");         
      })
    }
  }
}



}
