import { Component, OnInit, ViewChild } from '@angular/core';
import { ClienteService } from '../../../service/cliente.service';
import { Cliente } from '../../../model/cliente';
import { MatSort, MatPaginator, MatTableDataSource } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-list-cliente',
  templateUrl: './list-cliente.component.html',
  styleUrls: ['./list-cliente.component.css']
})
export class ListClienteComponent implements OnInit {

  clientes:Cliente[];
  searchaClient:string;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort:MatSort;

  dataSource:MatTableDataSource<Cliente>;
  displayedColumns = ['id','nombre','ci','telefono','direccion','alta','accion'];
  
  constructor(
    private route: ActivatedRoute,
    private clienteService: ClienteService,
      private router: Router,
      private flashMensaje:FlashMessagesService
      ) {
        this.dataSource=new MatTableDataSource;
       }

      ngAfterViewInit(){
        this.dataSource.paginator=this.paginator;
        this.dataSource.sort=this.sort;
      }

      ngOnInit() {
    this.list();
}

list(){
  
  this.clienteService.getClientes().subscribe( clientes => {
    this.clientes=clientes 
    this.dataSource.data =this.clientes;
  },error => {
    console.log(error)
    this.flashMensaje.show('Error al conectar con el servidor...',{cssClass:'alert-danger'})
    }
  );
  
}

search(searchaClient){
  this.clienteService.getBuscar(this.searchaClient).subscribe( clientes=>{
    this.clientes=clientes;
    this.dataSource.data=this.clientes;
  })
}

  applyFilter(){
    if(this.searchaClient !=""){
      this.dataSource.data=this.dataSource.data.filter(res=>{
        return res.nombre.toLocaleLowerCase().match(this.searchaClient.toLocaleLowerCase());
      });
    } else if(this.searchaClient=="") {
      this.ngOnInit();
    }
}

 addCliente(): void {
  this.router.navigate(['add-cliente']);
};

    remove(cliente: Cliente){
      if(confirm("Seguro que quieres eliminar: "+cliente.nombre+","+cliente.apellido)){
        this.clienteService.delete(cliente).subscribe(() =>{
          this.list();
        }, error =>{
          this.flashMensaje.show("No se puede eliminar cliente.",{cssClass:'alert-danger'})
        }) 
     }  
      }

}



