import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Proveedor } from '../model/proveedor';
import { GLOBAL } from './global';

const httpOptions = { headers:new HttpHeaders({
  'Content-Type':'application/json',
  'Authorization':localStorage.getItem("Authorization")
})}

@Injectable({
  providedIn: 'root'
})
export class ProveedorService {
  public url:string;
  constructor(public http:HttpClient) {
    this.url=GLOBAL.url;
  }
 getProveedors(){
   return this.http.get<Proveedor[]>(this.url+'/proveedor', httpOptions);
 }

 getProveedorN(){
  return this.http.get<Proveedor>(this.url+'/proveedor/proveedorn', httpOptions);
}
 getBuscar(nombre:string){
  return this.http.get<Proveedor[]>(this.url+'/proveedor/buscar/'+nombre, httpOptions);
}

 
   getProveedorId(id){
   return this.http.get<Proveedor>(this.url+'/proveedor/'+id, httpOptions);
 }
  
 save(proveedor: Proveedor){
   return this.http.post<Proveedor>(this.url+'/proveedor', proveedor, httpOptions);
   }
  
   update(proveedor: Proveedor){
    return this.http.put<Proveedor>(this.url+'/proveedor', proveedor, httpOptions);
    }

   delete(proveedor: Proveedor){
    return this.http.post<Proveedor>(this.url+'/proveedor/eliminar',proveedor, httpOptions);
    }



   

}