import { Component, OnInit, ViewChild } from '@angular/core';
import { CreditoAbono } from '../../../model/credito-Abono';
import { CreditoService } from '../../../service/credito.service';
import { Credito } from '../../../model/credito';
import { AddCreditoComponent } from '../add-credito/add-credito.component';
import { FlashMessagesModule, FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-add-abono',
  templateUrl: './add-abono.component.html',
  styleUrls: ['./add-abono.component.css']
})
export class AddAbonoComponent implements OnInit {

  @ViewChild(AddCreditoComponent) hijo: AddCreditoComponent;

  credSel:number;
  importAb:number;
  total:number;
  abono:CreditoAbono;
  creditoAbono:CreditoAbono[];
  credito:Credito;
  idUser:number;
  idC:number;
  dataSource;
  displayedColumns = ['usuario','fecha','importe'];

  constructor(
    private creditoService:CreditoService,
    public flashMessaje: FlashMessagesService
  ) {
   }


  ngOnInit() {
    this.idUser=parseInt(localStorage.getItem("id"));
  }

  recibirDato(credito){
       this.total=credito.saldo;
       this.credSel=credito.saldo;
       this.idC=credito.c;

       this.creditoService.getAbono(this.idC).subscribe(data =>{
        this.creditoAbono=data;
        console.log(this.creditoAbono)
        this.dataSource=this.creditoAbono;
      })
  }

  abonar(){
    this.abono={ importe:this.importAb, user:{id:this.idUser},credito:{id:this.idC}};
    console.log(JSON.stringify(this.abono))
    this.creditoService.saveAbono(this.abono).subscribe(()=>{
      this.flashMessaje.show('Registro guardado',{cssClass:'alert-success'})
      this.hijo.filtrar();
      this.creditoService.getAbono(this.idC).subscribe(data =>{
        this.creditoAbono=data;
        this.dataSource=this.creditoAbono;
      })
    },error=>{
      console.log(error)
      this.flashMessaje.show(error.messages,{cssClass:'alert-danger'})
    })
  }

 
}
