import { Component, OnInit, ViewChild, Inject, Output, EventEmitter } from '@angular/core';
import { MarcaService } from '../../../service/marca.service';
import { Marca } from '../../../model/marca';
import { Router } from '@angular/router';
import { MatPaginator, MatSort, MatTableDataSource, MAT_DIALOG_DATA } from '@angular/material';
import { HttpHeaders } from '@angular/common/http';
@Component({
  selector: 'app-list-marca',
  templateUrl: './list-marca.component.html',
  styleUrls: ['./list-marca.component.css']
})
export class ListMarcaComponent implements OnInit {
  @Output() marcaE=new EventEmitter();
  

  errorMessage:string
  marcas:Marca[];
  marca:Marca;
  dataSource;
  displayedColumns = ['id','descripcion','estado','editar','eliminar'];
  

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort:MatSort;

  applyFilter(filterValue:string){
  this.dataSource.filter=filterValue.trim().toLowerCase();

  }
  constructor(
    private marcaService:MarcaService,
    private router:Router,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit() {
    this.list();
  }

  list(){
    this.marcaService.getMarcas().subscribe( marcas => {
      this.marcas=marcas 
      this.dataSource =new MatTableDataSource(this.marcas);
    },error => {
        this.errorMessage=<any>error;
         console.log(this.errorMessage);
      }
      );
  }

editar(marca){
    this.marcaE.emit({fa:marca});
  }

  remove(marca:Marca){
    if(confirm("Seguro que quieres eliminar: "+marca.descripcion)){
      this.marcaService.delete(marca).subscribe(() =>{
        this.list();
      }, error =>{
       window.alert("Error no se puede eliminar")
      }) 
   }  
    }

}
