import { Component, OnInit, ViewChild } from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router, ActivatedRoute } from '@angular/router';
import { MatDialog, MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { CompraService } from 'src/app/service/compra.service';
import { Compra } from 'src/app/model/compra';
import { DetalleCompraComponent } from '../detalle-compra/detalle-compra.component';

@Component({
  selector: 'app-list-compra',
  templateUrl: './list-compra.component.html',
  styleUrls: ['./list-compra.component.css']
})
export class ListCompraComponent implements OnInit {

  compras:Compra[];
  dataSource;
  bfecha:Date;
  searchCompra:string;
  displayedColumns = ['id','nombre','ruc','subTotal','alta','detalle'];
  
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort:MatSort;


constructor(
      private route: ActivatedRoute,
      private compraService:CompraService,
      private router: Router,
      private flashMensaje:FlashMessagesService,
      public dialog: MatDialog
      ) { }

      openDialog(compra) {
        let open=this.dialog.open(DetalleCompraComponent, {
          height: '100%',
          width: '60%',
          data:[]=compra,
          panelClass:['colorDialogDetalle']
        });
      }
     
ngOnInit() {
  this.list();    
}
list(){
  this.compraService.getCompras().subscribe( compras => {
    this.compras=compras 
    this.dataSource =new MatTableDataSource(this.compras);
  },error => {
    this.flashMensaje.show('Error al conectar con servidor..',{cssClass:'alert-danger', timeout:4000})
    }
    );
}
 addCompra(): void {
  this.router.navigate(['add-compra']);
};

bfechaC(){

  this.compraService.getPorFecha(this.bfecha).subscribe(result=>{
    this.compras=result;
    this.dataSource.data=this.compras;
  })

}

    
applyFilter(){
  if(this.searchCompra !=""){
    this.dataSource.data=this.dataSource.data.filter(res=>{
      return res.proveedor.nombre.toLocaleLowerCase().match(this.searchCompra.toLocaleLowerCase());
    });
  } else if(this.searchCompra=="") {
    this.ngOnInit();
  }
}

}
