import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { User } from '../../../model/user';
import { UserService } from '../../../service/user.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';


@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.css']
})
export class AddUserComponent implements OnInit {
  
  
  user:User;
  users:User[];
  userEdit:User;
  enable:boolean;

  constructor(private _userService:UserService,
              private flashMessaje:FlashMessagesService,
              ) { 
                this.enable==true;
                this.dataSource=new MatTableDataSource;
                this.user={id:null,username:null,lastname:null,firstname:null,email:null,password:null,enabled:true};
              }

  
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort:MatSort;

  dataSource:MatTableDataSource<User>;
  displayedColumns :string[]= ['codigo','username','lastname','firstname','email','password','accion'];
  

applyFilter(filterValue:string){
this.dataSource.filter=filterValue.trim().toUpperCase();
}

        
      ngAfterViewInit(){
        this.dataSource.paginator=this.paginator;
        this.dataSource.sort=this.sort;
      }

      ngOnInit() {
        this.list();
    }

    list(){
      this._userService.listUser().subscribe(data=>{
        this.users=data;
        this.dataSource.data=this.users;
      },error => {
        console.log(error)
        this.flashMessaje.show(error,{cssClass:'alert-danger'})
      }
      );
    }  
      
    

  registerUser(){
    this._userService.registerUser(this.user).subscribe(() =>{
      this.flashMessaje.show('Registro guardado..',{cssClass:'alert-success', timeout:4000})
      this.list();
  },error => {
    this.flashMessaje.show(error.error.message,{cssClass:'alert-danger', timeout:4000})
  }
  );
  }

  update(user){
    user.password="";
    this.userEdit=user;
    this.user=this.userEdit;
  }



  remove(use){
    if(confirm("Seguro que quieres eliminar: "+use.nombre+","+use.apellido)){
      this._userService.removerUser(use).subscribe(() =>{
        this.flashMessaje.show('Registro eliminado..',{cssClass:'alert-success', timeout:4000})
      this.list();
      }, error =>{
        this.flashMessaje.show('Error no se puede eliminar el usuario ya esta utilizada por otro registro..',{cssClass:'alert-danger'})
      }) 
   }  
    }

}
