import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Compra } from '../model/compra';
import { GLOBAL } from './global';

const httpOptions = { headers:new HttpHeaders({
  'Content-Type':'application/json',
  'Authorization':localStorage.getItem("Authorization")
})}

@Injectable({
  providedIn: 'root'
})
export class CompraService {

  public url:string;
  constructor(public http:HttpClient) {
    this.url=GLOBAL.url;
  }
 
 getCompras(){
   return this.http.get<Compra[]>(this.url+'/compra',httpOptions);
 }

 getCompraId(){
  return this.http.get<Compra[]>(this.url+'/compra/ultId',httpOptions);
}

getCompraMes(ano){
  return this.http.get<any>(this.url+'/compra/compradia/'+ano, httpOptions);
}
   
 save(compra: Compra){
   return this.http.post(this.url+'/compra', compra,httpOptions);
   }

   saveDetalle(detalleCompra){
    return this.http.post<any>(this.url+'/detalleCompra' , detalleCompra,httpOptions);
    }
    informInicioCompra(){
      return this.http.get<any>(this.url+'/compra/infomes', httpOptions);
    }

    detalleCompra(id){
      return this.http.get<any>(this.url+'/detalleCompra'+id, httpOptions);
    }
    getPorFecha(ano){
      return this.http.get<Compra[]>(this.url+'/compra/fecha/'+ano, httpOptions);
    }
}
