import { Component } from '@angular/core';
import { Proveedor } from '../../../model/proveedor';
import { ProveedorService } from '../../../service/proveedor.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-add-proveedor',
  templateUrl: './add-proveedor.component.html',
  styleUrls: ['./add-proveedor.component.css']
})
export class AddProveedorComponent  {

    public proveedor:Proveedor=new Proveedor;
  constructor(
          private proveedorService:ProveedorService,
          public flashMessaje:FlashMessagesService
          ) { }

  save(proveedor: Proveedor){
    this.proveedorService.save(this.proveedor).subscribe(() =>{
      this.flashMessaje.show('Registro guardado..',{cssClass:'alert-success', timeout:4000})
     
    },error => {
      this.flashMessaje.show(error.error.message,{cssClass:'alert-danger', timeout:4000})
    }
    );
  }
  

}
