import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddUserComponent } from './component/user/add-user/add-user.component';
import { InicioComponent } from './component/inicio/inicio.component';
import { ListClienteComponent } from './component/cliente/list-cliente/list-cliente.component';
import { AddClienteComponent } from './component/cliente/add-cliente/add-cliente.component';
import { ListVentaComponent } from './component/venta/list-venta/list-venta.component';
import { AddVentaComponent } from './component/venta/add-venta/add-venta.component';
import { ListCompraComponent } from './component/compra/list-compra/list-compra.component';
import { AddCompraComponent } from './component/compra/add-compra/add-compra.component';
import { ListProductoComponent } from './component/producto/list-producto/list-producto.component';
import { AddProductoComponent } from './component/producto/add-producto/add-producto.component';
import { ListProveedorComponent } from './component/proveedor/list-proveedor/list-proveedor.component';
import { AddFamiliaComponent } from './component/familia/add-familia/add-familia.component';
import { AddMarcaComponent } from './component/marca/add-marca/add-marca.component';
import { AddProveedorComponent } from './component/proveedor/add-proveedor/add-proveedor.component';
import { AddAbonoComponent } from './component/credito/add-abono/add-abono.component';
import { EditClienteComponent } from './component/cliente/edit-cliente/edit-cliente.component';
import { EditProveedorComponent } from './component/proveedor/edit-proveedor/edit-proveedor.component';
import { EditProductoComponent } from './component/producto/edit-producto/edit-producto.component';
import { LoginComponent } from './component/user/login/login.component';
import { AuthGuard } from './auth.guard';






const appRoutes: Routes = [
  { path: '', component: InicioComponent, canActivate:[AuthGuard]},
  { path: 'user', component: AddUserComponent, canActivate:[AuthGuard] },
  { path: 'cliente', component: ListClienteComponent, canActivate:[AuthGuard] },
  { path: 'add-cliente', component: AddClienteComponent, canActivate:[AuthGuard]},
  { path: 'edit-cliente/:id', component: EditClienteComponent, canActivate:[AuthGuard]},
  { path: 'add-compra', component: AddCompraComponent, canActivate:[AuthGuard]},
  { path: 'list-compra', component: ListCompraComponent, canActivate:[AuthGuard]},
  { path: 'add-venta', component: AddVentaComponent, canActivate:[AuthGuard]},
  { path: 'list-venta', component: ListVentaComponent, canActivate:[AuthGuard]},
  { path: 'add-producto', component: AddProductoComponent, canActivate:[AuthGuard]},
  { path: 'edit-producto/:id', component: EditProductoComponent, canActivate:[AuthGuard]},
  { path: 'list-producto', component: ListProductoComponent, canActivate:[AuthGuard]},
  { path: 'add-proveedor', component: AddProveedorComponent, canActivate:[AuthGuard]},
  { path: 'list-proveedor', component: ListProveedorComponent, canActivate:[AuthGuard]},
  { path: 'edit-proveedor/:id', component: EditProveedorComponent, canActivate:[AuthGuard]},
  { path: 'add-familia', component: AddFamiliaComponent, canActivate:[AuthGuard]},
  { path: 'add-marca', component: AddMarcaComponent, canActivate:[AuthGuard]},
  { path: 'add-credito', component: AddAbonoComponent, canActivate:[AuthGuard]},
  { path: 'login', component: LoginComponent}
 // { path: '**', component: ErrorComponent , canActivate:[AuthGuard]}
	];

export const appRoutingProviders: any[] = [];
export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes);