import { Component, OnInit, ViewChild } from '@angular/core';
import { Chart } from 'chart.js';
import { AppComponent } from 'src/app/app.component';
import { UserService } from 'src/app/service/user.service';
import { FacturaService } from 'src/app/service/factura.service';
import {  DatePipe } from '@angular/common';
import { CompraService } from 'src/app/service/compra.service';

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.css']
})
export class InicioComponent implements OnInit {

  @ViewChild(AppComponent)  inicioApp: AppComponent;

  
  BarChart=[];
  costo=[];
  fecha=[];
  compreMes:number;
  ventasMes:number;
  

  public selectedMoment = new Date();

  constructor(private userService:UserService,
              private facturaService: FacturaService,
              private compraService: CompraService) { }

  ngOnInit() {
 
    this.userService.getUserDetails().subscribe(result =>{
      localStorage.setItem("id", result.id);
    })
    
    this.barChart();
    this.compraMes();
    this.ventaMes();

  }

  compraMes(){
    var datePipe=new DatePipe('en-US');
    this.compraService.getCompraMes(datePipe.transform(new Date(),'yyyy-MM')).subscribe(result=>{
      this.compraMes=result;
    })
  }

  ventaMes(){
    var datePipea=new DatePipe('en-US');
    this.facturaService.getVentaMes(datePipea.transform(new Date(),'yyyy-MM')).subscribe(result=>{
      this.ventasMes=result;
    })
  }

  barChart(){

    this.facturaService.informInicioFactura().subscribe(result=>{
      result.forEach(element => {
        this.costo.push(element[0]);
        this.fecha.push(element[1]);
      });
      this.BarChart=new Chart('barChart',{
        type:'bar',
        data:{
          labels:this.fecha,
          datasets:[{
            data:this.costo,
            fill:false,
            lineTension:0.2,
            borderColor:"rgb(0,0,0)",
            backgroundColor: 'rgb(0,123,255)',
            borderWidth:1
          }]
        },
        options:{
          title:{
            text:'Ventas del Ao',
            display:true
          },
          scales: {
            yAxes:[{
              ticks:{
                beginAtZero:true
              }
            }]
          }
        }
      })
    });
        

  }


}
