import { HttpClient, HttpHeaders, HttpErrorResponse, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Cliente } from '../model/cliente'; 
import { GLOBAL } from './global';


const httpOptions = { headers:new HttpHeaders({
  'Content-Type':'application/json',
  'Authorization':localStorage.getItem("Authorization")
})}

@Injectable()
export class ClienteService {
  public url:string;
  constructor(public http:HttpClient) {
    this.url=GLOBAL.url;
  }
    
    getClientes(){
      return this.http.get<Cliente[]>(this.url+'/cliente', httpOptions);
  }

  getClienteNA(){
    return this.http.get<Cliente>(this.url+'/cliente/clientena', httpOptions);
}

  getBuscar(nombre:string){
    return this.http.get<Cliente[]>(this.url+'/cliente/buscar/'+nombre, httpOptions);
  }

    getClienteId(id){
    return this.http.get<Cliente>(this.url+'/cliente/'+id, httpOptions);
  }
    
  save(cliente: Cliente){
    return this.http.post<Cliente>(this.url+'/cliente', cliente,httpOptions);
    }

    update(cliente: Cliente){
      return this.http.put<Cliente>(this.url+'/cliente', cliente,httpOptions);
      }

    delete(cliente:Cliente){
      return this.http.post<Cliente>(this.url+'/cliente/eliminar',cliente,httpOptions);;
    }
   
  
  }