import { Component, OnInit, ViewChild } from '@angular/core';
import { FacturaService } from '../../../service/factura.service';
import { Factura } from '../../../model/factura';
import { MatDialog, MatTableDataSource } from '@angular/material';
import { Cliente } from '../../../model/cliente';
import { ProductoService } from '../../../service/producto.service';
import { Producto } from '../../../model/producto';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ProdDialogComponent } from '../../compra/prod-dialog/prod-dialog.component';
import { ClienteDialogComponent } from '../cliente-dialog/cliente-dialog.component';
import { ClienteService } from 'src/app/service/cliente.service';


@Component({
  selector: 'app-add-venta',
  templateUrl: './add-venta.component.html',
  styleUrls: ['./add-venta.component.css']
})
export class AddVentaComponent implements OnInit {

  public ultimoI:Array<any>=[];
  ultimoCodigo:number;

  idUser:number;

  cliente:Cliente;
  id:number;
  nombre:string;

  producto:Producto;
  codigo:string;
  idPro:number;
  descripcion:string;
  precio:number;
  subTotal:number;
  cantidad:number;
  stock:string;

  factura:Factura;
  total:number;

  ctotal:number;
  cpago:number;
  ccambio:number;
  value:boolean;

  public detalle:Array<any>=[];
  public detalleFactura:Array<any>=[];

  estado:boolean;

  displayedColumns =['n','codigo','cantidad','descripcion','precioU','precioT','eliminar'];
  dataSource=new MatTableDataSource(this.detalle);

  constructor(public dialog: MatDialog,
              private productoService:ProductoService,
              private facturaService:FacturaService,
              private flashMensaje:FlashMessagesService,
              private clienteService:ClienteService){
              this.ctotal=0;
              this.estado==false;

  }


  ngOnInit(){
    this.idUser=parseInt(localStorage.getItem("id"));
    this.getClienteNA();
    this.ultimoId(); 
  }

  getClienteNA(){
    this.clienteService.getClienteNA().subscribe(result=>{
      this.id=result.id;
      this.nombre=result.nombre+", "+result.apellido;

    })
  }

  openDialog() {
    let open=this.dialog.open(ClienteDialogComponent, {
      height: '100%',
      width: '100%',
      panelClass:['colorDialog']
    });
 
    open.afterClosed().subscribe(result => {
      this.cliente = result;
      this.id=this.cliente.id;
      this.nombre=this.cliente.nombre+" , "+this.cliente.apellido;
    });
  }

  openDialogProd() {
    let open=this.dialog.open(ProdDialogComponent, {
      height: '100%',
      width: '100%',
      panelClass:['colorDialog']
    });
 
    open.afterClosed().subscribe(result => {
      this.codigo=result.codigoProducto;
      this.descripcion=result.descripcion;
      this.precio=result.precioVenta;
      this.idPro=result.id;
      this.stock=result.existencia+"  Stock";
    });
  }

  resta(){ 
    this.ccambio=this.cpago-this.ctotal;

  }
  

  agregarItem(){
    if(this.cantidad==null){
      this.flashMensaje.show('Debe ingresar la cantidad',{cssClass:'alert-danger', timeout:4000})
    } else {
      var id=this.idPro;
      var codigo=this.codigo;
      var descripcion=this.descripcion;
      var cantidad=this.cantidad;
      var precio=this.precio;
      var subtotal=this.precio*this.cantidad;
      this.agregarProducto(id,codigo,descripcion,cantidad,precio,subtotal);

    }
  }
  
  agregarProducto(id,codigo,descripcion,cantidad,precio,subtotal){
  this.detalleFactura.push({cantidad:cantidad, precio:subtotal, producto:{id:id}})
  this.detalle.push({id:id,codigo:codigo,descripcion:descripcion,cantidad:cantidad,precio:precio,subtotal:subtotal});
  this.dataSource.data=this.detalle;
  this.subTotal=this.getTotalCost();
  this.ctotal=this.getTotalCost();
    this.idPro=null;
    this.codigo=null;
    this.producto=null;
    this.descripcion=null;
    this.precio=null;
    this.cantidad=null;
    this.subTotal=null;
    this.stock=null;
  }
  
  getTotalCost(){
    return this.detalle.map(t=>t.subtotal).reduce((acc, value) => acc+value, 0);
  }

  delete(index:number){
    this.detalleFactura.splice(index,1);
    this.detalle.splice(index,1);
    
    this.ctotal=this.getTotalCost();
    return this.dataSource.data=this.detalle
  }

  playAudio(){
    let audio=new Audio();
    audio.src=('../../../assets/sonido/Ding.wav');
    audio.load();
    audio.play();
  }

  playAudioE(){
    let audio=new Audio();
    audio.src=('../../../assets/sonido/Error.wav');
    audio.load();
    audio.play();
  }

  ultimoId(){
    this.facturaService.getFacturaId().subscribe(data =>{
      this.ultimoI=data;
      this.ultimoI.forEach(ultimo =>{
        this.ultimoCodigo=ultimo.id+1;
      } )
    })
  }

registrar(){

  if(this.ctotal==0 || this.cpago==null || this.ccambio==null ){
    this.playAudioE();
    this.flashMensaje.show('EL ITEM ESTA VACIO NO PUEDE IMPRIMIR',{cssClass:'alert-danger', timeout:4000})
} else {

  this.factura={id:null,subTotal:this.ctotal,anulado:"NO",estado:this.estado,cliente:{id:this.id,nombre:null},user:{id:this.idUser}}
  this.facturaService.save(this.factura).subscribe(() =>{

      this.facturaService.saveDetalle(this.detalleFactura).subscribe(() =>{
        this.flashMensaje.show('Venta realizada... Total:'+this.ctotal+'Gs',{cssClass:'alert-success', timeout:4000})
        this.playAudio(); 
        this.ctotal=0;
        this.detalle=[];
        this.detalleFactura=[];
        this.estado=false;
        this.dataSource.data=this.detalle;
        this.ultimoId();
        this.getClienteNA();
        this.cpago=null;
        this.ccambio=null;
      },err =>{
        this.playAudioE();
        this.flashMensaje.show('Error detalle..',{cssClass:'alert-danger', timeout:4000})
      })


  },err =>{
    this.playAudioE();
    console.log(err)
    this.flashMensaje.show('Error factura..'+err.message,{cssClass:'alert-danger', timeout:4000})
  })

}
}


}
