import { Component, OnInit } from '@angular/core';
import { ProdDialogComponent } from '../prod-dialog/prod-dialog.component';
import { MatDialog, MatTableDataSource } from '@angular/material';
import { ProvDialogComponent } from '../prov-dialog/prov-dialog.component';
import { Proveedor } from 'src/app/model/proveedor';
import { Router } from '@angular/router';
import { Producto } from 'src/app/model/producto';
import { Compra } from 'src/app/model/compra';
import { CompraService } from 'src/app/service/compra.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ProveedorService } from 'src/app/service/proveedor.service';

@Component({
  selector: 'app-add-compra',
  templateUrl: './add-compra.component.html',
  styleUrls: ['./add-compra.component.css']
})
export class AddCompraComponent implements OnInit {

  producto:Producto;
  ctotal:number;
  codigo:string;
  idPro:number;
  descripcion:string;
  precio:number;
  subTotal:number;
  cantidad:number;
  unidadMedida:string;

  proveedor:Proveedor;
  id:number;
  nombre:string;
  compra:Compra;
  idUser:number;
  ultimoCodigo:number;
  ultimoI:Compra[];

  public detalle:Array<any>=[];
  detalleCompra: Array<any>=[];
  

  displayedColumns =['n','codigo','cantidad','descripcion','precioU','precioT','eliminar'];
  dataSource=new MatTableDataSource(this.detalle);
  
  constructor(public dialog: MatDialog,
              private router: Router,
              private compraService: CompraService,
              private flashMensaje:FlashMessagesService,
              private proveedorService:ProveedorService) {
                this.ctotal=0;
               }

  openDialogProd() {
    let open=this.dialog.open(ProdDialogComponent, {
      data: { name: 'austin' },
      height: '100%',
      width: '100%',
      panelClass:['colorDialog']
    });
 
    open.afterClosed().subscribe(result => {
      this.codigo=result.codigoProducto;
      this.idPro=result.id;
      this.descripcion=result.descripcion;
      this.precio=result.precioCosto;
      this.unidadMedida=result.unidadMedida.descripcion;
    });
  }

  openDialog() {
    let open=this.dialog.open(ProvDialogComponent, {
      height: '100%',
      width: '100%',
      panelClass:['colorDialog']
    });
 
    open.afterClosed().subscribe(result => {
      this.proveedor = result;
      this.id=result.id;
      this.nombre=result.nombre;
    });
  }
  ngOnInit() {
    this.idUser=parseInt(localStorage.getItem("id"));
    this.getProveedorN();
    this.ultimoId(); 
  }

  getProveedorN(){
    this.proveedorService.getProveedorN().subscribe(result=>{
      this.id=result.id;
      this.nombre=result.nombre;
    })
  }

  ultimoId(){
    this.compraService.getCompraId().subscribe(data =>{
      this.ultimoI=data;
      this.ultimoI.forEach(ultimo =>{
        this.ultimoCodigo=ultimo.id+1;
      } )
    })
  }
  listCompra(): void{
    this.router.navigate(['list-compra']);
  }

  agregarItem(){
    if(this.unidadMedida=="CENTIMETRO"){
      if(this.cantidad==null){
        this.flashMensaje.show('Debe ingresar la cantidad',{cssClass:'alert-danger', timeout:4000})
      } else {
        var id=this.idPro;
        var codigo=this.codigo;
        var descripcion=this.descripcion;
        var cantidad=this.cantidad;
        var precio=this.precio;
        var subtotal=this.precio*this.cantidad/1000;
        this.agregarProducto(id,codigo,descripcion,cantidad,precio,subtotal);
  
      }
    } else {
      if(this.cantidad==null){
       this.flashMensaje.show('Debe ingresar la cantidad',{cssClass:'alert-danger', timeout:4000})
      } else {
        var id=this.idPro;
        var codigo=this.codigo;
        var descripcion=this.descripcion;
        var cantidad=this.cantidad;
        var precio=this.precio;
        var subtotal=this.precio*this.cantidad;
        this.agregarProducto(id,codigo,descripcion,cantidad,precio,subtotal);
  
      }
    }
 
  }

  agregarProducto(id,codigo,descripcion,cantidad,precio,subtotal){
    this.detalleCompra.push({cantidad:cantidad, precio:subtotal, producto:{id:id}});
    this.detalle.push({id:id,codigo:codigo,descripcion:descripcion,cantidad:cantidad+"/"+this.unidadMedida,precio:precio,subtotal:subtotal});
    this.dataSource.data=this.detalle;
    this.subTotal=this.getTotalCost();
    this.ctotal=this.getTotalCost();
      this.idPro=null;
      this.codigo=null;
      this.producto=null;
      this.descripcion=null;
      this.precio=null;
      this.cantidad=null;
      this.subTotal=null;
      this.unidadMedida=null;
    }

    getTotalCost(){
      return this.detalle.map(t=>t.subtotal).reduce((acc, value) => acc+value, 0);
    }
    delete(index:number){
      this.detalleCompra.splice(index,1);
      this.detalle.splice(index,1)
      
      this.ctotal=this.getTotalCost();
      return this.dataSource.data=this.detalle
    }

    registrar(){

      if(this.ctotal==0 ){
        //this.playAudioE();
        this.flashMensaje.show('EL ITEM ESTA VACIO NO PUEDE IMPRIMIR',{cssClass:'alert-danger', timeout:4000})
    } else {
    
      this.compra={id:null,subTotal:this.ctotal,proveedor:{id:this.id},user:{id:this.idUser}}
      console.log(JSON.stringify(this.compra))
      this.compraService.save(this.compra).subscribe(() =>{    
          console.log(JSON.stringify(this.detalleCompra));
           this.compraService.saveDetalle(this.detalleCompra).subscribe(() =>{
            
            this.flashMensaje.show('Compra realizada... Total: '+this.ctotal+'Gs',{cssClass:'alert-success', timeout:2000})
            this.playAudio();
            this.ctotal=0;
            this.detalle=[];
            this.detalleCompra=[];
            this.dataSource.data=this.detalle;
            this.ultimoId();
            this.getProveedorN();
          },err =>{
            console.log(err)
           this.playAudioE();
            this.flashMensaje.show('Error en el detalle..',{cssClass:'alert-danger', timeout:4000})
          })
          
    
      
      },err =>{
        this.playAudioE();
        this.flashMensaje.show('Error en la compra..',{cssClass:'alert-danger', timeout:4000})
      })
    
    }
    
    }
    
    playAudio(){
      let audio=new Audio();
      audio.src=('../../../assets/sonido/Ding.wav');
      audio.load();
      audio.play();
    }
  
    playAudioE(){
      let audio=new Audio();
      audio.src=('../../../assets/sonido/Error.wav');
      audio.load();
      audio.play();
    }

}
