import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Factura } from '../model/factura';
import { DetalleFactura } from '../model/detalle-Factura';
import { GLOBAL } from './global';

const httpOptions = { headers:new HttpHeaders({
  'Content-Type':'application/json',
  'Authorization':localStorage.getItem("Authorization")
})}

@Injectable({
  providedIn: 'root'
})
export class FacturaService {
   
  public url:string;
  constructor(public http:HttpClient) {
    this.url=GLOBAL.url;
  }
  
  getFactura(){
    return this.http.get<Factura[]>(this.url+'/factura', httpOptions);
  }
  getFacturas(){
    return this.http.get<Factura>(this.url+'/factura', httpOptions);
  }


  getFacturaId(){
    return this.http.get<Factura[]>(this.url+'/factura/ultId', httpOptions);
  }

  save(factura: Factura){
    return this.http.post<Factura>(this.url+'/factura', factura, httpOptions);
    }

    anular(factura: Factura){
      return this.http.post<Factura>(this.url+'/factura/a', factura, httpOptions);
      }

    saveDetalle(detalleFactura){
      return this.http.post<any>(this.url+'/detalle' , detalleFactura, httpOptions);
      }

      getDetalleId(id:number){
        return this.http.get<DetalleFactura[]>(this.url+'/detalle/'+id, httpOptions);
      }
    

      informe(inicio:Date,fin:Date){
        return this.http.get<Factura[]>(this.url+'/factura/informe/'+inicio+'/'+fin)
      }

      informInicioFactura(){
        return this.http.get<any>(this.url+'/factura/infomes', httpOptions);
      }

      getPorFecha(ano){
        return this.http.get<Factura[]>(this.url+'/factura/fecha/'+ano, httpOptions);
      }

      getVentaMes(ano){
        return this.http.get<any>(this.url+'/factura/ventadia/'+ano, httpOptions);
      }
 
 
}
