import { Producto } from '../../../model/producto';
import { Familia } from '../../../model/familia';
import { Marca } from '../../../model/marca';
import { UnidadMedida } from '../../../model/unidad-medida';
import { ProductoService } from '../../../service/producto.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-edit-producto',
  templateUrl: './edit-producto.component.html',
  styleUrls: ['./edit-producto.component.css']
})
export class EditProductoComponent implements OnInit {

  producto: Producto;
  familiaId:number;
  familia:Familia[];
  marcaId:number;
  marca:Marca[];
  unidadMedidaId:number;
  unidadMedida:UnidadMedida[];
  
  
  constructor(
  private route:ActivatedRoute,
  private productoService: ProductoService,
  private router: Router,
  public flashMensaje:FlashMessagesService ) {  }

  ngOnInit() {
    this.route.params.forEach((params: Params) =>{
      let id = params['id'];
      
      this.productoService.getProductoConsultaId(id).subscribe(data => {
        this.producto=data;
        this.familiaId=this.producto.familia.id;
        this.familia=[{id:this.producto.familia.id,descripcion:this.producto.familia.descripcion,estado:null}];

        this.marcaId=this.producto.marca.id;
        this.marca=[{id:this.producto.marca.id,descripcion:this.producto.marca.descripcion,estado:null}];

        this.unidadMedidaId=this.producto.unidadMedida.id;
        this.unidadMedida=[{id:this.producto.unidadMedida.id,descripcion:this.producto.unidadMedida.descripcion}];
      
      });
    });
    
    
    
  }
  
  
  update(producto: Producto){
    console.log(producto)
    this.productoService.save(producto).subscribe(producto =>{
      this.router.navigate(['list-producto']);
    },error => {
      this.flashMensaje.show(error.error.message,{cssClass:'alert-danger', timeout:4000})
    });
  }
  


}
