import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Credito } from '../model/credito';
import { CreditoAbono } from '../model/credito-Abono';
import { GLOBAL } from './global';

const httpOptions = { headers:new HttpHeaders({
  'Content-Type':'application/json',
  'Authorization':localStorage.getItem("Authorization")
})}

@Injectable({
  providedIn: 'root'
})
export class CreditoService {

  public url:string;
  constructor(public http:HttpClient) {
    this.url=GLOBAL.url;
  }

  getCreditoTodo(id:number){
    return this.http.get<Credito[]>(this.url+'/credito/'+id,httpOptions);
  }

  getCreditoPago(id:number,estado:string){
    return this.http.get<Credito[]>(this.url+'/credito/'+id+'/'+estado,httpOptions);
  }
   
 save(credito:Credito){
   return this.http.post<Credito>(this.url+'/credito', credito,httpOptions);
   }

   saveAbono(abono:CreditoAbono){
     return this.http.post<CreditoAbono>(this.url+'/abono', abono,httpOptions);
   }

   getAbono(id:number){
     return this.http.get<CreditoAbono[]>(this.url+'/abono/'+id,httpOptions);
   }

 


  }