import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { ProductoService } from 'src/app/service/producto.service';
import { Producto } from 'src/app/model/producto';

@Component({
  selector: 'app-prod-dialog',
  templateUrl: './prod-dialog.component.html',
  styleUrls: ['./prod-dialog.component.css']
})
export class ProdDialogComponent implements OnInit {

  productos:Producto[];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort:MatSort;

  dataSource:MatTableDataSource<Producto>;
  displayedColumns = ['codigo','descripcion','existencia','familia','marca','compra','accion'];

  constructor(
    private productoService:ProductoService
  ) { this.dataSource=new MatTableDataSource; }

  ngOnInit() {
    this.list();
  }

  list(){

    this.productoService.getProducto().subscribe( productos => {
      this.productos=productos 
      this.dataSource.data =this.productos;
    },error => {
      console.log(error)
     // this.flashMensaje.show('Error al conectar con el servidor...',{cssClass:'alert-danger'})
      }
      );

  }

}
